.Slot {

}

.Slot__title {
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 0.1em;
}

.Slot__wrap {
  background-color: #fff;
  border: 0;
  border-radius: 0.5em;
  font-size: 2em;
  height: 3.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Slot__inner {
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.Slot__inner--going {
  -webkit-filter: blur(0.5em);
          filter: blur(0.5em);
  -webkit-transition: 0s 0s !important;
  -o-transition: 0s 0s !important;
  transition: 0s 0s !important;
}


